"use client";
import React, { useState } from "react";
import { Modal } from "@/components/ui/modal";
import jsPDF from "jspdf";

interface Payslip {
    id: string;
    payPeriod: string;
    grossPay: number;
    deductions: number;
    netPay: number;
    status: "Paid" | "Pending";
    employeeName: string;
    employeeId: string;
    department: string;
    earnings: { label: string; amount: number }[];
    deductionsDetails: { label: string; amount: number }[];
}

const payslipsData: Payslip[] = [
    {
        id: "1",
        payPeriod: "October 2025",
        grossPay: 6500,
        deductions: 1430,
        netPay: 5070,
        status: "Paid",
        employeeName: "John Doe",
        employeeId: "EMP-2024-001",
        department: "Engineering",
        earnings: [
            { label: "Base Salary", amount: 6000 },
            { label: "Performance Bonus", amount: 500 },
        ],
        deductionsDetails: [
            { label: "Federal Tax", amount: 980 },
            { label: "Social Security", amount: 403 },
            { label: "Health Insurance", amount: 47 },
        ],
    },
    // Add more payslips here
];

export default function PayslipsPage() {
    const [selectedPayslip, setSelectedPayslip] = useState<Payslip | null>(null);

    const handleView = (payslip: Payslip) => setSelectedPayslip(payslip);
    const handleClose = () => setSelectedPayslip(null);

    const handleDownloadPDF = () => {
        if (!selectedPayslip) return;

        const doc = new jsPDF();
        doc.setFontSize(16);
        doc.text("Payslip", 105, 15, { align: "center" });

        doc.setFontSize(12);
        doc.text(`Employee Name: ${selectedPayslip.employeeName}`, 20, 30);
        doc.text(`Employee ID: ${selectedPayslip.employeeId}`, 20, 37);
        doc.text(`Department: ${selectedPayslip.department}`, 20, 44);
        doc.text(`Pay Period: ${selectedPayslip.payPeriod}`, 20, 51);

        let y = 60;
        doc.text("Earnings:", 20, y);
        selectedPayslip.earnings.forEach((item) => {
            y += 7;
            doc.text(`${item.label}: $${item.amount.toFixed(2)}`, 30, y);
        });
        y += 7;
        doc.text(`Gross Pay: $${selectedPayslip.grossPay.toFixed(2)}`, 30, y);

        y += 10;
        doc.text("Deductions:", 20, y);
        selectedPayslip.deductionsDetails.forEach((item) => {
            y += 7;
            doc.text(`${item.label}: $${item.amount.toFixed(2)}`, 30, y);
        });
        y += 7;
        doc.text(`Total Deductions: $${selectedPayslip.deductions.toFixed(2)}`, 30, y);

        y += 10;
        doc.text(`Net Pay: $${selectedPayslip.netPay.toFixed(2)}`, 20, y);

        doc.save(`Payslip_${selectedPayslip.payPeriod}.pdf`);
    };

    return (
        <div className="p-6">
            <h1 className="text-2xl font-bold mb-6">Payslips</h1>

            <div className="overflow-x-auto">
                <table className="min-w-full border border-gray-200">
                    <thead className="bg-gray-100">
                        <tr>
                            <th className="py-2 px-4 border">Pay Period</th>
                            <th className="py-2 px-4 border">Gross Pay</th>
                            <th className="py-2 px-4 border">Deductions</th>
                            <th className="py-2 px-4 border">Net Pay</th>
                            <th className="py-2 px-4 border">Status</th>
                            <th className="py-2 px-4 border">Action</th>
                        </tr>
                    </thead>
                    <tbody>
                        {payslipsData.map((p) => (
                            <tr key={p.id} className="text-center">
                                <td className="py-2 px-4 border">{p.payPeriod}</td>
                                <td className="py-2 px-4 border">${p.grossPay.toFixed(2)}</td>
                                <td className="py-2 px-4 border">${p.deductions.toFixed(2)}</td>
                                <td className="py-2 px-4 border">${p.netPay.toFixed(2)}</td>
                                <td className="py-2 px-4 border">{p.status}</td>
                                <td className="py-2 px-4 border">
                                    <button
                                        onClick={() => handleView(p)}
                                        className="bg-blue-600 text-white px-3 py-1 rounded hover:bg-blue-700"
                                    >
                                        View
                                    </button>
                                </td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>

            <Modal isOpen={!!selectedPayslip} onClose={handleClose} className="max-w-2xl p-6">
                {selectedPayslip && (
                    <div className="space-y-4">
                        <h2 className="text-xl font-bold">Payslip Details</h2>
                        <div className="space-y-1">
                            <p><strong>Name:</strong> {selectedPayslip.employeeName}</p>
                            <p><strong>Employee ID:</strong> {selectedPayslip.employeeId}</p>
                            <p><strong>Department:</strong> {selectedPayslip.department}</p>
                            <p><strong>Pay Period:</strong> {selectedPayslip.payPeriod}</p>
                        </div>

                        <div className="mt-4">
                            <h3 className="font-semibold">Earnings</h3>
                            <table className="min-w-full border border-gray-200">
                                <tbody>
                                    {selectedPayslip.earnings.map((e) => (
                                        <tr key={e.label}>
                                            <td className="border py-1 px-2">{e.label}</td>
                                            <td className="border py-1 px-2">${e.amount.toFixed(2)}</td>
                                        </tr>
                                    ))}
                                    <tr className="font-bold">
                                        <td className="border py-1 px-2">Gross Pay</td>
                                        <td className="border py-1 px-2">${selectedPayslip.grossPay.toFixed(2)}</td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>

                        <div className="mt-4">
                            <h3 className="font-semibold">Deductions</h3>
                            <table className="min-w-full border border-gray-200">
                                <tbody>
                                    {selectedPayslip.deductionsDetails.map((d) => (
                                        <tr key={d.label}>
                                            <td className="border py-1 px-2">{d.label}</td>
                                            <td className="border py-1 px-2">${d.amount.toFixed(2)}</td>
                                        </tr>
                                    ))}
                                    <tr className="font-bold">
                                        <td className="border py-1 px-2">Total Deductions</td>
                                        <td className="border py-1 px-2">${selectedPayslip.deductions.toFixed(2)}</td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>

                        <div className="mt-4 font-bold text-lg">
                            Net Pay: ${selectedPayslip.netPay.toFixed(2)}
                        </div>

                        <div className="mt-6 flex justify-end">
                            <button
                                onClick={handleDownloadPDF}
                                className="bg-green-600 text-white px-4 py-2 rounded hover:bg-green-700"
                            >
                                Download PDF
                            </button>
                        </div>
                    </div>
                )}
            </Modal>
        </div>
    );
}
